import { Component } from '@angular/core';
import { AlertController, IonicPage, LoadingController, NavController } from 'ionic-angular';
import { AbstractControl, FormBuilder, FormGroup, Validators } from "@angular/forms";
import { of as observableOf } from 'rxjs/observable/of';
import { map } from 'rxjs/operators/map';
import { City, CityProvider } from "../../providers/city/city";
import { Plan } from "../../providers/plan/plan";
import { Questionary } from "../../providers/questionary/questionary";
import { Neighborhood, NeighborhoodProvider } from "../../providers/neighborhood/neighborhood";
import { MetricItem, MetricValue } from "../prioritization/prioritization";
import { DatabaseProvider } from "../../providers/database/database";
import { HomePage } from "../home/home";
import { RespondentProvider } from "../../providers/respondent/respondent";
import { PrioritizationProvider } from "../../providers/prioritization/prioritization";
import { Storage } from "@ionic/storage";
import { Question } from '../../providers/question/question';

@IonicPage()
@Component({
  selector: 'page-respondent-profile',
  templateUrl: 'respondent-profile.html',
})
export class RespondentProfilePage {
  city: City;
  cities: City[] = [];
  plan: Plan;
  questionaries: Questionary[];
  residenceNeighborhoods: Neighborhood[];
  jobNeighborhoods: Neighborhood[];
  respondentForm: FormGroup;
  residenceTimeList: ResidenceTime[] = [];
  salaryRangeList: SalaryRange[] = [];
  residenceTimeName: string = "";
  salaryRangeName: string = "";
  metricItems: MetricItem[] = [];
  jobNeighborhoodDisabled: boolean = true;
  isSameJobCity: string = "hide";
  jobCity: City;
  loader: any;
  cityName: string;
  QUESTIONARY_CASE_TEST = "Urbanização";

  constructor(public navCtrl: NavController, private databaseProvider: DatabaseProvider, private cityProvider: CityProvider, private formBuilder: FormBuilder, private respondentProvider: RespondentProvider, private neighborhoodProvider: NeighborhoodProvider, private priorizationProvider: PrioritizationProvider, private alertCtrl: AlertController, private storage: Storage, public loadingCtrl: LoadingController) {
    this.createForm();
    this.getAllCities();
    this.loadMetrics();
    this.loadResidenceTime();
    this.loadSalaryRange();
    setTimeout(() => {
      this.existsRespondent();
    }, 1000);
  }

  existsRespondent() {
    this.loader = this.loadingCtrl.create();
    this.loader.present();
    this.storage.get('respondent')
      .then((respondent: Respondent) => {
        this.setFormData();
        if (respondent != null) {
          this.respondentForm.controls['cpf'].setValue(respondent.cpf);
          this.respondentForm.controls['email'].setValue(respondent.email);
          this.respondentForm.controls['residenceTimeRange'].setValue(respondent.residenceTimeRange);
          this.respondentForm.controls['salaryRange'].setValue(respondent.salaryRange);
          this.respondentForm.controls['residenceNeighborhood'].setValue(respondent.residenceNeighborhood);
          this.respondentForm.controls['jobCity'].setValue(respondent.jobCity);
          if (respondent.jobNeighborhood != null) {
            this.respondentForm.controls['jobNeighborhood'].setValue(respondent.jobNeighborhood);
            this.jobCity = respondent.jobCity;
            this.loadJobNeighborhoods();
            this.isSameJobCity = "";
          }
        }
      })
      .catch(() => {
        this.navigateBack();
      });
  }

  setFormData() {
    let entities = ['city', 'plan', 'isRuralZone', 'questionaries'];
    this.databaseProvider.verifyEntities(entities)
      .then((data) => {
        if (data.length > 0 && data.filter((data) => data == false).length == 0) {
          this.setEntities();
        } else {
          this.navigateBack();
        }
      })
      .catch(() => {
        this.navigateBack();
      });
  }

  navigateBack() {
    this.navCtrl.setRoot(HomePage, {}).then(this.loader.dismiss());
  }

  setEntities() {
    this.storage.get('city')
      .then((data) => {
        if (data != null) {
          this.city = data;
          this.cityName = this.city.name;
          this.storage.get('plan')
            .then((data) => {
              if (data != null) {
                this.plan = data;
                this.storage.get('questionaries')
                  .then((data) => {
                    this.questionaries = data;
                    this.loadResidenceNeighborhoods();
                  });
              } else {
                this.navigateBack();
              }
            })
            .catch(() => this.navigateBack());
        } else {
          this.navigateBack();
        }
      })
      .catch(() => this.navigateBack());
  }

  getAllCities() {
    this.cityProvider.getAllCities()
      .then((cities: City[]) => {
        if (cities != null) {
          this.cities = this.sortAscCollection(cities);
        } else {
          this.showAlertGetAllCities();
        }
      })
      .catch(() => {
        this.showAlertGetAllCities();
      });
  }

  showAlertGetAllCities() {
    this.cities = [];
    let alert = this.alertCtrl.create({
      title: 'Oops!',
      message: 'Não foi possível acessar os dados do servidor. Por favor, tente novamente.',
      buttons: [{
        text: "Tentar novamente",
        handler: () => {
          this.getAllCities();
        }
      }]
    });
    alert.present();
  }

  loadMetrics() {
    //------------------------CARREGA OS ITENS DE MÉTRICAS-----------------------------
    //1 - GUT, 2 - ESCALA QUALITATIVA, (3-17) - Métricas do questionário de teste
    this.priorizationProvider.getMetricItems(1)
      .then((metricItems: MetricItem[]) => {
        if (metricItems != null) {
          this.metricItems = metricItems;
          //------------------------CARREGA OS VALORES DE MÉTRICA-----------------------------
          this.priorizationProvider.getMetricValues()
            .then((result: MetricValue[]) => {
              if (result != null) {
                //------------------------ASSOCIA OS VALORES AOS ITENS-----------------------------
                for (let i = 0; i < this.metricItems.length; i++) {
                  let metricItem = this.metricItems[i];
                  for (let j = 0; j < result.length; j++) {
                    let metricValue = result[j];
                    if (metricItem.id == metricValue.metricItemId) {
                      this.metricItems[i].metricValues.push(metricValue);
                    }
                  }
                }
                //------------------------SALVA OS ITENS DE MÉTRICA-----------------------------
                this.storage.set('metricItems', this.metricItems);
              } else {
                this.showAlertLoadMetrics();
              }
            })
            .catch(() => {
              this.showAlertLoadMetrics();
            });
        } else {
          this.showAlertLoadMetrics();
        }
      })
      .catch(() => {
        this.showAlertLoadMetrics();
      });
  }

  showAlertLoadMetrics() {
    this.metricItems = [];
    let alert = this.alertCtrl.create({
      title: 'Oops!',
      message: 'Não foi possível acessar os dados do servidor. Por favor, tente novamente.',
      buttons: [{
        text: "Tentar novamente",
        handler: () => {
          this.loadMetrics();
        }
      }]
    });
    alert.present();
  }

  createForm() {
    this.respondentForm = this.formBuilder.group({
      cpf: ['', [Validators.required], this.validateCPF.bind(this)],
      email: [''],
      residenceTimeRange: [''],
      residenceNeighborhood: ['', [Validators.required]],
      jobCity: [''],
      jobNeighborhood: [''],
      salaryRange: ['']
    });
    this.respondentForm.controls['residenceTimeRange'].setValue(0);
    this.respondentForm.controls['salaryRange'].setValue(0);
  }

  loadResidenceNeighborhoods() {
    this.neighborhoodProvider.getAllNeighborhoodsByCity(this.city)
      .then((neighborhoods: Neighborhood[]) => {
        if (neighborhoods != null) {
          this.residenceNeighborhoods = this.sortAscCollection(neighborhoods);
          this.storage.set('neighborhoods', this.residenceNeighborhoods);
          this.loader.dismiss();
        } else {
          this.showAlertLoadResidenceNeighborhoods();
        }
      })
      .catch(() => {
        this.showAlertLoadResidenceNeighborhoods();
      });
  }

  showAlertLoadResidenceNeighborhoods() {
    this.loader.dismiss();
    this.residenceNeighborhoods = [];
    let alert = this.alertCtrl.create({
      title: 'Oops!',
      message: 'Não foi possível acessar os dados do servidor. Por favor, tente novamente.',
      buttons: [{
        text: "Tentar novamente",
        handler: () => {
          this.loadResidenceNeighborhoods();
        }
      }]
    });
    alert.present();
  }

  selectCity(city) {
    this.jobCity = city;
    if (this.compareFn(this.jobCity, this.city)) {
      this.isSameJobCity = "";
      this.respondentForm.controls['jobNeighborhood'].setValue('');
    } else {
      this.isSameJobCity = "hide";
    }
  }

  loadJobNeighborhoods() {
    this.neighborhoodProvider.getAllNeighborhoodsByCity(this.jobCity)
      .then((neighborhoods: Neighborhood[]) => {
        if (neighborhoods != null) {
          this.jobNeighborhoodDisabled = false;
          this.jobNeighborhoods = this.sortAscCollection(neighborhoods);
          if (this.compareFn(this.jobCity, this.city)) {
            this.isSameJobCity = "";
          } else {
            this.isSameJobCity = "hide";
          }
        } else {
          this.showAlertLoadJobNeighborhoods();
        }
      })
      .catch(() => {
        this.showAlertLoadJobNeighborhoods();
      });
  }

  showAlertLoadJobNeighborhoods() {
    this.jobNeighborhoods = [];
    let alert = this.alertCtrl.create({
      title: 'Oops!',
      message: 'Não foi possível acessar os dados do servidor. Por favor, tente novamente.',
      buttons: [{
        text: "Tentar novamente",
        handler: () => {
          this.loadJobNeighborhoods();
        }
      }]
    });
    alert.present();
  }

  validate(cpf) {
    cpf = cpf.replace(/[^\d]+/g, '');
    if (cpf == '') return false;
    // Elimina CPFs invalidos conhecidos
    if (cpf.length != 11 ||
      cpf == "00000000000" ||
      cpf == "11111111111" ||
      cpf == "22222222222" ||
      cpf == "33333333333" ||
      cpf == "44444444444" ||
      cpf == "55555555555" ||
      cpf == "66666666666" ||
      cpf == "77777777777" ||
      cpf == "88888888888" ||
      cpf == "99999999999")
      return false;
    // Valida 1o digito
    let add = 0;
    for (let i = 0; i < 9; i++)
      add += parseInt(cpf.charAt(i)) * (10 - i);
    let rev = 11 - (add % 11);
    if (rev == 10 || rev == 11)
      rev = 0;
    if (rev != parseInt(cpf.charAt(9)))
      return false;
    // Valida 2o digito
    add = 0;
    for (let i = 0; i < 10; i++)
      add += parseInt(cpf.charAt(i)) * (11 - i);
    rev = 11 - (add % 11);
    if (rev == 10 || rev == 11)
      rev = 0;
    return rev == parseInt(cpf.charAt(10));
  }

  validateCPF(control: AbstractControl) {
    let isValid: boolean = this.validate(control.value.toString());
    return observableOf(!isValid).pipe(
      map(result => result ? { invalid: true } : null)
    );
  }

  sortAscCollection(collection) {
    return collection.sort(function (a, b) {
      return a.name.localeCompare(b.name);
    });
  }

  saveRespondentInfo() {
    this.loader = this.loadingCtrl.create();
    this.loader.present();
    let cpf = this.respondentForm.controls['cpf'];
    let email = this.respondentForm.controls['email'];
    let residenceNeighborhood = this.respondentForm.controls['residenceNeighborhood'];
    let residenceTimeRange = this.respondentForm.controls['residenceTimeRange'];
    let jobCity = this.respondentForm.controls['jobCity'];
    let jobNeighborhood = this.respondentForm.controls['jobNeighborhood'];
    let salaryRange = this.respondentForm.controls['salaryRange'];
    if (!this.respondentForm.valid) {
      if (!cpf.valid) {
        cpf.markAsTouched();
      }
      if (!residenceNeighborhood.valid) {
        residenceNeighborhood.markAsTouched();
      }
      this.loader.dismiss();
    }
    else {
      let respondent = new Respondent();
      respondent.cpf = cpf.value;
      respondent.email = email.value;
      respondent.residenceTimeRange = residenceTimeRange.value;
      respondent.residenceNeighborhood = residenceNeighborhood.value;
      respondent.jobCity = jobCity.value;
      respondent.jobNeighborhood = jobNeighborhood.value;
      respondent.salaryRange = salaryRange.value;
      this.respondentProvider.getRespondentByCPF(respondent.cpf)
        .then((result: Respondent) => {
          // respondente já está cadastrado
          if (!!result) {
            respondent.id = result.id;
            this.respondentProvider.updateRespondent(respondent)
              .then(() => {
                this.navigate(respondent);
              })
              .catch(() => {
                this.tryAgainSaveRespondent();
              });
          } else { // respondente não está cadastrado
            this.respondentProvider.insertRespondent(respondent)
              .then((data: Respondent) => {
                respondent.id = data.id;
                this.navigate(respondent);
              })
              .catch(() => {
                this.tryAgainSaveRespondent();
              });
          }
          if(respondent.id % 2 == 0){
            this.storage.set('useGame', true);
          }else{
            this.storage.set('useGame', false);
          }
        })
        .catch(() => {
          this.tryAgainSaveRespondent();
        });
    }
  }

  tryAgainSaveRespondent() {
    let alert = this.alertCtrl.create({
      title: 'Oops!',
      message: 'Não foi possível acessar os dados do servidor. Por favor, tente novamente.',
      buttons: [{
        text: "Tentar novamente",
        handler: () => {
          this.saveRespondentInfo();
        }
      }]
    });
    this.loader.dismiss();
    alert.present();
  }

  navigate(respondent) {
    this.storage.set('respondent', respondent);
    this.navCtrl.setRoot('QuestionariesListPage', {}).then(this.loader.dismiss());
  }

  getResidenceTimeNameByValue(event) {
    let residenceTimeChosen: number = event.value;
    this.residenceTimeName = this.residenceTimeList[residenceTimeChosen].name;
  }

  getSalaryRangeNameByValue(event) {
    let salaryRangeChosen: number = event.value;
    this.salaryRangeName = this.salaryRangeList[salaryRangeChosen].name;
  }

  loadResidenceTime() {
    let residenceTime = new ResidenceTime();
    residenceTime.name = "Menos de um 1 ano";
    residenceTime.value = 0;
    this.residenceTimeList.push(residenceTime);
    residenceTime = new ResidenceTime();
    residenceTime.name = "De 1 a 5 anos";
    residenceTime.value = 1;
    this.residenceTimeList.push(residenceTime);
    residenceTime = new ResidenceTime();
    residenceTime.name = "De 5 a 10 anos";
    residenceTime.value = 2;
    this.residenceTimeList.push(residenceTime);
    residenceTime = new ResidenceTime();
    residenceTime.name = "De 10 a 20 anos";
    residenceTime.value = 3;
    this.residenceTimeList.push(residenceTime);
    residenceTime = new ResidenceTime();
    residenceTime.name = "Mais de 20 anos";
    residenceTime.value = 4;
    this.residenceTimeList.push(residenceTime);
    this.residenceTimeName = this.residenceTimeList[0].name;
  }

  loadSalaryRange() {
    let salaryRange = new SalaryRange();
    salaryRange.name = "Até 2 Salários Mínimos";
    salaryRange.value = 0;
    this.salaryRangeList.push(salaryRange);
    salaryRange = new SalaryRange();
    salaryRange.name = "De 2 a 4 Salários Mínimos";
    salaryRange.value = 1;
    this.salaryRangeList.push(salaryRange);
    salaryRange = new SalaryRange();
    salaryRange.name = "De 4 a 10 Salários Mínimos";
    salaryRange.value = 2;
    this.salaryRangeList.push(salaryRange);
    salaryRange = new SalaryRange();
    salaryRange.name = "De 10 a 20 Salários Mínimos";
    salaryRange.value = 3;
    this.salaryRangeList.push(salaryRange);
    salaryRange = new SalaryRange();
    salaryRange.name = "Acima de 20 Salários Mínimos";
    salaryRange.value = 4;
    this.salaryRangeList.push(salaryRange);
    this.salaryRangeName = this.salaryRangeList[0].name;
  }

  compareFn(e1: any, e2: any): boolean {
    return e1 && e2 ? e1.id === e2.id : e1 === e2;
  }

}

export class ResidenceTime {
  name: string;
  value: number;
}

export class SalaryRange {
  name: string;
  value: number;
}

export class Respondent {
  id: number;
  cpf: string;
  email: string;
  residenceTimeRange: number;
  residenceNeighborhood: Neighborhood;
  jobCity: City;
  jobNeighborhood: Neighborhood;
  salaryRange: number;
  created_at: string;
}



